import { Link, graphql, useStaticQuery } from "gatsby"
import * as React from "react"

const AppHeader = ({setTheme}:any) => {
  const [sm1,setSm1] = React.useState(false)
  const data = useStaticQuery(graphql`
  query HeaderQuery {
    site {
      siteMetadata {
        title
        author
        nav {
            text
            to
        }
      }
    }
  }
`)
let mode:any = undefined
const [st1,setSt1] = React.useState('?')
const auto_ =() => {
  if (localStorage.ti==1) {
    if (window.matchMedia('(prefers-color-scheme:dark)').matches) {
      document.documentElement.classList.add('dark')
      if(typeof(setTheme)=="function")setTheme('dark')
    } else {
      document.documentElement.classList.remove('dark')
      if(typeof(setTheme)=="function")setTheme('light')
    }
  }
}
const auto = () =>{ 
  setSt1('ğŸ’»')
  localStorage.ti = 1
  auto_()
}
const dark = () => {
  setSt1('ğŸŒ™')
  if(typeof(setTheme)=="function")setTheme('dark')
  localStorage.ti = 0
  document.documentElement.classList.add('dark')
}

const light = () => {
  setSt1('â˜€')
  if(typeof(setTheme)=="function")setTheme('light')
  localStorage.ti = 2
  document.documentElement.classList.remove('dark')
}
const theme = () => {
  if (localStorage.ti==undefined || localStorage.ti==0) {
    auto()
    if (mode!=undefined) {
      mode.addEventListener('change',()=>auto_()) 
    } else {
      mode = window.matchMedia('(prefers-color-scheme:dark)')
      mode.addEventListener('change',()=>auto_()) 
    }
  } else if(localStorage.ti==1){
    light()
    if (mode!=undefined)mode.removeEventListener('change',()=>auto_())
  } else {
    dark()
  }
}

React.useEffect(()=>{
  mode = window.matchMedia('(prefers-color-scheme:dark)')
  if(localStorage.ti==undefined || localStorage.ti==0) {
    setSt1('ğŸŒ™')
    if(typeof(setTheme)=="function")setTheme('dark')
  } else if(localStorage.ti==1){
    setSt1('ğŸ’»')
    mode.addEventListener('change',()=>auto_())
  } else {
    setSt1('â˜€')
    if(typeof(setTheme)=="function")setTheme('light')
  }
  return (()=>{
   mode.removeEventListener('change',()=>auto_())
  })
},[])
    return (
        <>
            <div className="h-14 w-full z-40 overflow-hidden md:overflow-visible ease-in-out duration-400 transition-all fixed flex top-0 bg-white/80 dark:bg-gray-950/80 dark:text-gray-50 backdrop-blur-md items-center select-none">
                <Link to="/" className="text-lg font-bold absolute left-2">{data.site.siteMetadata.title}</Link>
                <div className="absolute right-2 flex-row md:flex hidden gap-3 font-semibold">
                {data.site.siteMetadata.nav.map((item:any,i:Number)=><Link key={'header-'+i} to={item.to}>{item.text}</Link>)}
                <div onClick={theme}>æ¨¡å¼:{st1}</div>
                </div>
                <a className="md:hidden absolute right-2 flex-col flex gap-[5px]" onClick={()=>setSm1(!sm1)}>
                  <span className={`h-[2px] w-6 dark:bg-gray-50 bg-gray-950 transition-all ease-linear ${sm1?'rotate-45 absolute':''}`}></span>
                  <span className={`h-[2px] w-6 dark:bg-gray-50 bg-gray-950 transition-all ${sm1?'opacity-0':''}`}></span>
                  <span className={`h-[2px] w-6 dark:bg-gray-50 bg-gray-950 transition-all ease-linear ${sm1?'-rotate-45 absolute':''}`}></span>
                </a>
            </div>
            <div className={`w-full bg-white dark:bg-gray-950 fixed z-20 top-0 h-full md:hidden transition-all duration-[300ms] ${sm1?'':'translate-y-[-120vh] opacity-0'}`}>
                  <div className="flex flex-col gap-3 font-semibold mt-16 items-center">{data.site.siteMetadata.nav.map((item:any,i:Number)=><Link onClick={()=>setSm1(false)} className="w-[92%] pt-2 pb-2 border-b border-gray-300 dark:border-gray-700" key={'header-1-'+i} to={item.to}>{item.text}</Link>)}
                  <div onClick={theme}>æ¨¡å¼:{st1}</div>
                  </div>
            </div>
        </>
    )
}

export default AppHeader