import * as React from "react"
import AppHeader from "./AppHeader"
import AppFooter from "./AppFooter"
type Props = {
    children: React.ReactNode
    setTheme:any
}

const AppContent = ({ children,setTheme }:Props) => {
    return (
        <>
            <main className="w-full h-full">
                <AppHeader setTheme={setTheme} />
                <div className="w-[98%] min-h-[calc(120vh-402px)] text-gray-950 mt-16 m-auto left-0 right-0 md:w-4/5 lg:w-3/5">{children}</div>
                <AppFooter/>
            </main>
        </>
    )
}
export default AppContent